.tel {
	@include media('<sm') {
		height: 240px;
	}
	@include media('<xs') {
		height: 200px;
	}

	display: flex;

	height: 280px;

	background: url(../images/architecture.jpg) no-repeat 50% 50% / cover;

	align-items: center;
	justify-content: center;

	&__link {
		@include media('<sm') {
			font-size: 32px;
			line-height: 1.2;

			padding: 15px 20px;
		}
		@include media('<xs') {
			font-size: 22px;
			line-height: 1.25;

			padding: 10px;
		}

		font-family: $oswald;
		font-size: 44px;
		line-height: 1.1364;

		display: block;

		padding: 15px 40px;

		transition: background $t;
		text-decoration: none;

		color: inherit;
		background: $yellowsea;

		body:not(.mobile) &:hover {
			color: inherit;
			background: lighten($yellowsea, 10%);
		}
	}
}
