.customer{
  display: flex;
  flex-direction: column;
  margin-top: 46px;
  &-item {
    max-width: 900px;
    display: flex;
    justify-content: space-between;
    padding-bottom: 40px;
    border-bottom: 2px solid #ffb001;
    margin-bottom: 35px;
    &__text{
      margin-right: 10px;
      &-title{
        color: #2b2f35;
        font-size: 26px;
      }
      &-subtitle{
        color: #666666;
        font-style: italic;
        font-size: 16px;
      }
    }
  }
  &-item:last-child{
    margin-bottom: 0px;
  }

}
@media (max-width: 1000px){
  .customer{
    margin-top: 46px;
    &-item {
      padding-bottom: 35px;
      margin-bottom: 30px;
      &__text{
        &-title{
          font-size: 24px;
        }
        &-subtitle{
          font-size: 14px;
        }
      }
    }
  }
}
@media (max-width: 425px){
  .customer{
    margin-top: 30px;
    &-item {
      padding-bottom: 30px;
      margin-bottom: 25px;
      flex-direction: column;
      align-items: center;
      &__text{
        text-align: center;
        &-title{
          margin-bottom: 10px !important;
        } 
      }
      &__img{
        margin-top: 10px;
      }
    }
  }
}