.review {
	@include media('<md') {
		border-width: 10px;
	}

	position: relative;

	display: flex;
	flex-direction: column;

	width: 100%;
	padding: 20px;

	color: $dovegray;
	border: 20px solid $yellowsea;
	background: white;

	justify-content: space-between;

	&::after {
		@include triangle(40px 40px, $yellowsea, up-left);
		@include media('<md') {
			top: calc(100% + 10px);

			border-width: 20px;
		}

		position: absolute;
		top: calc(100% + 20px);
		left: 0;

		content: '';
	}
	&__title {
		color: #2b2f35;
		font-size: 24px;
		line-height: 1;
	}
	&__body {
		width: 100%;
	}

	&__text {
		font-size: 14px;
		line-height: 1.2858;
	}

	&__more {
		margin-top: 15px;
	}

	&__more-link {
		&::after {
			content: '  \2192';
		}
	}

	&__footer {
		display: flex;

		width: 100%;
		margin-top: 20px;
	}

	&__hero {
		width: 80px;

		align-self: flex-start;
	}

	&__image {
		display: block;

		max-width: 100%;
		height: auto;

		border: 1px solid $mercury;
	}

	&__signature {
		font-style: italic;

		width: calc(100% - 80px);
		padding: 0 0 0 20px;

		align-self: center;
	}
}
.review-inner{
	flex-direction: row;
	max-width: 900px;
	.review__right{
		.review__img{
			margin-left: 22px;
		}
	.review__left{
		.review__body{
			.review__text {
				font-size: 14px;
				color: #666666;
				line-height: 18px;
			}
		}
	}
	}
	.review__footer{
		margin-top: 25px;
		display: flex;
		align-items: center;
	}
	.review-inner__footer-text{
		font-style: italic;
		font-size: 14px;
		color: #666666;
	}
	footer{
		.review__hero{
			margin-right: 20px;
		}
		time{
			position: relative;
			margin: 0;
			margin-left: 20px;
			margin-right: 20px;
		}
		time::after{
			content: '|';
			position: absolute;
			right: -11px;
		}
		time::before{
			content: '|';
			position: absolute;
			left: -11px;
		}
	}
}
@media (max-width: 700px){
	.review-inner{
		flex-direction: column;
		.review__footer{
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
			.review__hero{
				margin-bottom: 10px;
			}
		}
	}
	.review-inner .review__right .review__img{
		margin: 0;
		margin-top: 20px;
		display: flex;
		justify-content: center;
	}
}