.card {
	@include margin-bottom(60px);

	display: flex;

	max-width: 1200px;

	flex-wrap: wrap;

	&__header {
		width: 100%;
		margin-bottom: 20px;
	}

	&__specification {
		@include media('<sm') {
			width: 100%;
			margin-top: 20px;
			padding-right: 0;

			order: 1;
		}

		width: calc(100% - 360px);
		padding-right: 40px;
	}

	&__photos {
		@include media('<xs') {
			width: 260px;
			margin: 0 auto;
		}

		width: 360px;
	}
}
