.crumbs {
	@include media('<md') {
		padding: 20px;
	}

	font-size: 14px;
	line-height: 1.4286;

	display: flex;

	padding: 20px 40px;

	color: $alto;
	background: $shark;

	flex-wrap: wrap;

	&__item {
		&:nth-last-child(n+2)::after {
			margin: 0 4px;

			content: '\002F';
		}
	}

	&__link {
		color: white;

		body:not(.mobile) &:hover {
			color: white;
		}
	}
}
.reviews-crumb{
	background: linear-gradient(to bottom, rgba(0,0,0,0.25), rgba(0,0,0,0) 20px),#2b2f35;
}
.reviews-nodecor{
	text-decoration: none;
}