.reviews {
	@include media('<md') {
		padding: 40px 20px;
	}

	position: relative;

	padding: 80px 40px;

	color: white;
	background: $shark;

	&__slider {
		@include media('<md') {
			margin: 30px -20px 0;
		}

		margin: 60px -40px 0;
	}

	&__item {
		@include media('<md') {
			padding: 0 20px 20px;
		}

		display: flex;

		height: auto;
		padding: 0 40px 40px;
	}

	&__nav {
		@include media('<md') {
			position: static;

			margin-top: 20px;

			align-items: center;
			justify-content: space-between;
		}

		position: absolute;
		top: 80px;
		right: 40px;

		display: flex;

		align-items: flex-end;
	}

	&__arrow {
		@include media('<md') {
			margin-left: 0;
		}

		margin-left: 20px;

		&._type_prev {
			@include media('<md') {
				order: -1;
			}
		}
	}
}
