.service {
	@include margin-top(1px);

	&__intro {
		@include media('<md') {
			padding: 40px 20px;
		}

		display: flex;

		padding: 80px 40px;

		transition: background $t;

		color: white;
		background: $shark;

		align-items: flex-start;

		.service._open & {
			background: $dodgerblue;
		}
	}

	&__hero {
		@include media('<sm') {
			display: none;
		}

		width: 200px;
	}

	&__image {
		display: block;

		max-width: 100%;
		height: auto;
	}

	&__info {
		@include media('<sm') {
			width: calc(100% - 60px);
			padding: 0 20px 0 0;
		}
		@include media('<xs') {
			width: calc(100% - 50px);
		}

		width: calc(100% - 260px);
		padding: 0 40px;
	}

	&__description {
		margin-top: 30px;
	}

	&__more {
		margin-top: 40px;
	}

	&__more-btn {
		font-family: $opensans;
		font-size: 13px;
		line-height: 1.5385;

		display: inline-block;

		padding: 10px 20px;

		cursor: pointer;
		transition: background $t;

		color: $shark;
		border: 0;
		background: $yellowsea;

		&::after {
			display: inline-block;

			margin-left: 7px;

			content: '\2192';
			transition: transform $t;
		}

		body:not(.mobile) &:hover {
			background: $chileanfire;
		}

		.service._open &::after {
			transform: rotate(-90deg);
		}
	}

	&__actions {
		@include media('<xs') {
			width: 50px;
		}

		width: 60px;
	}

	&__action {
		@include margin-top(1px);
		@include media('<xs') {
			width: 50px;
			height: 50px;
			padding-top: 8px;
		}

		font-size: 11px;

		display: block;

		width: 60px;
		height: 60px;
		padding: 12px 0 0;

		transition: color $t, background $t;
		text-align: center;
		text-decoration: none;

		background: white;

		&._type_drop {
			color: $dodgerblue;

			body:not(.mobile) &:hover {
				background: $azureradiance;
			}
		}

		&._type_phone {
			color: $chileanfire;

			body:not(.mobile) &:hover {
				background: $chileanfire;
			}
		}

		body:not(.mobile) &:hover {
			color: white;
		}
	}

	&__action-icon {
		display: block;

		margin: 0 auto 4px;

		fill: currentColor;
	}

	&__body {
		position: relative;

		overflow: hidden;

		height: 0;

		transition: height $t;

		background: white url(../images/lake.jpg) no-repeat 50% 0 / cover;

		&::before {
			@include triangle(60px 60px, $shark, up-left);
			@include media('<md') {
				left: 260px;

				border-width: 30px;
			}
			@include media('<sm') {
				left: 20px;
			}

			position: absolute;
			bottom: 100%;
			left: 280px;

			display: block;

			content: '';
			transition: transform $t, border-color $t;
		}

		.service._open & {
			height: auto;

			&::before {
				@include media('<md') {
					transform: translate3d(0,30px,0);
				}

				transform: translate3d(0,60px,0);

				border-top-color: $dodgerblue;
				border-left-color: $dodgerblue;
			}
		}
	}

	&__body-inner {
		@include media('<md') {
			padding: 40px 20px;
		}
		@include media('<sm') {
			display: block;
		}

		display: flex;

		padding: 80px 40px;
	}

	&__aside {
		@include media('<sm') {
			display: flex;

			width: auto;

			align-items: center;
			flex-wrap: wrap;
			justify-content: space-between;
		}

		width: 200px;
	}

	&__caption {
		@include media('<sm') {
			font-size: 20px;

			width: calc(100% - 200px);
			margin-top: 0;
			padding: 0 20px 0 0;

			border-top: 0;

			align-self: flex-start;
		}
		@include media('<xs') {
			width: 100%;
			padding: 0;
		}

		font-family: $opensans;
		font-size: 24px;
		line-height: 1.25;
	}

	&__btn-holder {
		@include media('<sm') {
			width: 200px;
			margin-top: 0;
			padding-top: 0;

			border-top: 0;

			align-self: flex-start;
		}
		@include media('<xs') {
			margin-top: 20px;
		}

		margin-top: 20px;
		padding-top: 40px;

		border-top: 3px solid $dodgerblue;
	}

	&__content {
		@include media('<sm') {
			width: auto;
			margin-top: 20px;
			padding: 0;
		}

		width: calc(100% - 200px);
		padding: 0 0 0 80px;
	}

	&__text {
		color: $bluewhale;
	}

	&__list {
		@include reset-list;
		@include media('<md') {
			column-count: 2;
		}
		@include media('<xs') {
			column-count: 1;
		}

		margin: 26px 0 0;

		column-count: 3;
		column-gap: 20px;
	}

	&__list-item {
		display: inline-block;

		width: 100%;
		padding: 4px 0 0 12px;

		&::before {
			display: block;
			float: left;

			width: 4px;
			height: 4px;
			margin: 8px 0 0 -12px;

			content: '';

			border-radius: 50%;
			background: $chileanfire;
		}
	}
}
