.consult {
	@include media('<md') {
		margin: 0 0 20px 20px;
		padding: 10px;
	}
	@include media('<xs') {
		float: none;

		margin: 0 0 20px;
	}

	font-family: $opensans;
	font-size: 13px;
	font-weight: 700;
	line-height: 1.5385;

	display: block;
	float: right;

	width: 200px;
	margin: 0 0 40px 40px;
	padding: 20px 10px;

	text-align: center;

	background: $linkwater;
}
