.gallery {
	@include media('<md') {
		padding: 40px 20px;
	}

	position: relative;

	padding: 80px 40px;

	background: $mercury;

	&__slider {
		@include media('<md') {
			margin-top: 30px;
		}

		margin-top: 60px;
	}

	&__image {
		display: block;

		width: 100%;
		height: auto;
	}

	&__nav {
		@include media('<md') {
			position: static;

			margin-top: 20px;

			align-items: center;
			justify-content: space-between;
		}

		position: absolute;
		top: 80px;
		right: 40px;

		display: flex;

		align-items: flex-end;
	}

	&__arrow {
		@include media('<md') {
			margin-left: 0;
		}

		margin-left: 20px;

		&._type_prev {
			@include media('<md') {
				order: -1;
			}
		}
	}
}
