.btn {
	font-family: $opensans;
	font-weight: 700;
	line-height: 1.5385;

	display: block;

	width: 100%;
	padding: 20px;

	cursor: pointer;
	transition: background $t;
	text-align: center;
	text-decoration: none;

	border: 0;
	outline: none;

	&._type_default {
		color: white;
		background: $chileanfire;

		body:not(.mobile) &:hover {
			color: white;
			background: lighten($chileanfire, 5%);
		}
	}

	&._type_info {
		color: white;
		background: $dodgerblue;

		body:not(.mobile) &:hover {
			color: white;
			background: $azureradiance;
		}
	}

	&._size_sm {
		font-size: 13px;
		line-height: 1.5385;

		padding: 10px 20px;
	}

	&._size_md {
		@include media('<sm') {
			padding: 10px 20px;
		}

		font-size: 13px;
		line-height: 1.5385;

		padding: 20px;
	}

	&._size_lg {
		font-size: 16px;
		line-height: 1.25;

		padding: 20px;
	}

