.services {
	@include media('<md') {
		padding: 40px 20px;
	}

	padding: 80px 40px;

	color: white;
	background: linear-gradient(to bottom, rgba(black, .25), rgba(black, 0) 20px), $shark;

	&__title {
		text-align: center;
	}

	&__list {
		@include media('<md') {
			margin: 0 -20px;
		}
		@include media('<xs') {
			margin: 0 -10px;
		}

		display: flex;

		margin: 0 -40px;

		flex-wrap: wrap;
	}

	&__item {
		@include media('<sm') {
			width: 50%;
		}
		@include media('<xs') {
			padding: 0 10px;
		}

		display: flex;
		flex-direction: column;

		width: 25%;
		margin: 40px 0 0;
		padding: 0 20px;

		text-align: center;

		justify-content: space-between;
	}

	&__hero {
		margin-bottom: 20px;
	}

	&__image {
		display: block;

		max-width: 100%;
		height: auto;
		margin: 0 auto;
	}

	&__caption {
		@include media('<md') {
			font-size: 18px;
		}

		font-family: $oswald;
		font-size: 24px;
		line-height: 1.1667;
	}

	&__link {
		color: white;

		body:not(.mobile) &:hover {
			color: $linkwater;
		}
	}

	&__item-footer {
		margin-top: 30px;
	}

	&__btn {
		@include media('<xs') {
			padding: 10px 15px;
		}

		font-family: $opensans;
		font-size: 13px;
		line-height: 1.5385;

		display: inline-block;

		padding: 10px 20px;

		cursor: pointer;
		transition: background $t;
		text-decoration: none;

		color: $shark;
		border: 0;
		background: $yellowsea;

		&::after {
			display: inline-block;

			margin-left: 7px;

			content: '\2192';
			transition: transform $t;
		}

		body:not(.mobile) &:hover {
			color: $shark;
			background: $chileanfire;
		}
	}
}
