.images {
	display: flex;

	flex-wrap: wrap;
	justify-content: flex-end;

	&__hero {
		width: 260px;
		margin: 0;
	}

	&__image {
		display: block;

		max-width: 100%;
		height: auto;
	}

	&__thumbs {
		@include media('<xs') {
			display: flex;

			width: 100%;
			margin: 20px 0 0;

			justify-content: center;
			order: 1;
		}

		position: relative;

		width: 80px;
		margin-right: 20px;
	}

	&__thumb {
		@include media('>=xs') {
			@include margin-top(20px);

			position: relative;

			height: 60px;

			cursor: pointer;

			background-repeat: no-repeat;
			background-position: 50% 50%;
			background-size: cover;
		}
		@include media('<xs') {
			display: block;

			width: 10px;
			height: 10px;
			margin: 0 5px;

			cursor: pointer;

			border: 2px solid $silver;
			background: none !important;
		}

		&::before {
			@include media('>=xs') {
				position: absolute;
				top: 0;
				left: 0;

				display: block;

				width: 100%;
				height: 100%;

				content: '';
				transition: border-color $t;

				border: 2px solid transparent;
			}
		}

		&._active {
			@include media('<xs') {
				border-color: $yellowsea;
			}

			&::before {
				@include media('>=xs') {
					border-color: $yellowsea;
				}
			}
		}
	}

	&__btn-holder {
		@include media('<xs') {
			padding: 20px 0 0;

			order: 1;
		}

		width: 100%;
		padding: 20px 20px 0 120px;
	}
}
