.price {
	overflow-x: auto;

	table {
		@include media('<sm') {
			font-size: 14px;
		}
		@include media('<xs') {
			font-size: 12px;
		}

		width: 100%;

		border-collapse: collapse;
	}

	th {
		@include media('<sm') {
			padding: 10px;
		}

		font-weight: 700;

		padding: 10px 20px;

		text-align: center;

		border: 0;
		background: $spindle;

		&:nth-child(n+2) {
			box-shadow: 1px 0 0 #86afd9 inset;
		}
	}

	td {
		@include media('<sm') {
			padding: 10px;
		}

		padding: 10px 20px;

		&:nth-child(n+2) {
			text-align: center;

			box-shadow: 1px 0 0 #b9d3eb inset;
		}
	}

	tr:nth-child(2n) td {
		background: $linkwater;
	}

	tr:nth-child(2n+1):last-child td {
		border-bottom: 1px solid #b9d3eb;
	}
}
