.recommendations {
	@include media('<md') {
		padding: 40px 20px;
	}

	padding: 80px 40px;

	background: $mercury;

	&__title {
		text-align: center;
	}

	&__list {
		@include reset-list;
		@include media('<md') {
			margin-top: 22px;
		}
		@include media('<sm') {
			column-count: 1;
			column-gap: 0;
		}

		font-size: 16px;
		line-height: 1.25;

		margin-top: 52px;

		column-count: 2;
		column-gap: 40px;
	}

	&__item {
		display: inline-block;

		width: 100%;
		padding: 8px 0 0 12px;

		&::before {
			display: block;
			float: left;

			width: 4px;
			height: 4px;
			margin: 9px 0 0 -12px;

			content: '';

			border-radius: 50%;
			background: $chileanfire;
		}
	}
}
