.paging {
	@include media('<md') {
		margin-top: 10px;
	}

	display: flex;

	margin: 40px -10px 0;

	flex-wrap: wrap;

	&__link {
		font-size: 14px;
		font-weight: 700;
		line-height: 38px;

		display: block;

		min-width: 40px;
		margin: 20px 10px 0;

		transition: color $t, border-color $t, background $t;
		text-align: center;
		text-decoration: none;

		color: $dustygray;
		border: 1px solid $dodgerblue;

		body:not(.mobile) &:hover {
			color: $dustygray;
			border-color: $yellowsea;
		}

		&._active,
		body:not(.mobile) &._active {
			color: white;
			border-color: $yellowsea;
			background: $yellowsea;
		}
	}
}
