// Fonts
$pfdintextcondpro: 'PF Din Text Cond Pro', sans-serif;
$opensans: 'Open Sans', sans-serif;
$oswald: 'Oswald', sans-serif;
$ptsans: 'PT Sans', sans-serif;

// Colors
$shark: #2b2f35;
$dodgerblue: #4ca2fa;
$chileanfire: #f27507;
$azureradiance: #009cf6;
$linkwater: #d9e8f5;
$yellowsea: #ffb001;
$bluewhale: #042f44;
$mercury: #e4e4e4;
$silver: #ccc;
$alto: #dcdcdc;
$dovegray: #666;
$spindle: #b9d3eb;
$dustygray: #999;

// Transition
$t: .3s ease;

// Breakpoints
$breakpoints: (
	'xs': 568px,
	'sm': 768px,
	'md': 1024px,
	'lg': 1240px
);
