.nav {
	@include media('>=sm','<md') {
		float: left;

		width: calc(100% - 220px);
		padding: 20px 0;
	}
	@include media('<sm') {
		order: -1;
	}

	&__open-button {
		@include media('>=sm') {
			display: none;
		}

		position: relative;

		display: block;

		width: 30px;
		height: 30px;

		cursor: pointer;

		border: 0;
		outline: none;
		background: none;

		&::before,
		&::after,
		& > i {
			position: absolute;
			left: 0;

			display: block;

			width: 30px;
			height: 3px;

			content: '';

			background: $shark;
		}

		&::before {
			top: calc(50% - 12px);
		}

		&::after {
			top: calc(50% + 8px);
		}

		& > i {
			top: calc(50% - 2px);
		}
	}

	&__close-button {
		@include media('>=sm') {
			display: none;
		}

		position: absolute;
		top: 25px;
		left: 20px;

		display: block;

		width: 30px;
		height: 30px;

		cursor: pointer;

		border: 0;
		outline: none;
		background: none;

		&::before,
		&::after {
			position: absolute;
			top: calc(50% - 1.5px);
			left: 0;

			display: block;

			width: 30px;
			height: 3px;

			content: '';

			background: $shark;
		}

		&::before {
			transform: rotate(45deg);
		}

		&::after {
			transform: rotate(-45deg);
		}
	}

	&__menu {
		@include media('<sm') {
			position: fixed;
			z-index: 100;
			top: 0;
			left: -300px;

			overflow-y: auto;

			width: 300px;
			height: 100%;
			padding: 80px 20px 20px;

			transition: left $t, opacity $t;

			opacity: 0;
			background: white;
			box-shadow: 0 0 20px rgba(black, .25);
		}

		.nav._open & {
			@include media('<sm') {
				left: 0;

				opacity: 1;
			}
		}
	}

	&__list {
		@include reset-list;
		@include media('<sm') {
			font-family: $oswald;
			font-size: 20px;
			line-height: 1.25;

			display: block;

			margin: 0;
		}

		font-family: $opensans;
		font-size: 14px;
		line-height: 1.2858;

		display: flex;

		margin: 0 -10px;
	}

	&__item {
		@include media('<sm') {
			@include margin-top(20px);

			margin: 0;
		}

		margin: 0 10px;
	}

	&__link {
		@include media('<sm') {
			color: inherit;
		}

		&._active {
			text-decoration: none;

			color: $chileanfire;
		}
	}
}
