.arrow {
	display: flex;

	width: 40px;
	height: 40px;
	padding: 0;

	cursor: pointer;
	transition: color $t, opacity $t;

	color: $dodgerblue;
	border: 1px solid;
	outline: none;
	background: none;

	align-items: center;
	justify-content: center;

	&._disabled {
		cursor: default;

		opacity: .5;
	}

	&__icon {
		display: block;

		fill: currentColor;

		.arrow._type_prev & {
			transform: rotate(180deg);
		}
	}

	body:not(.mobile) &:not(._disabled):hover {
		color: $yellowsea;
	}
}
