.header {
	@include media('>=sm','<md') {
		@include clearfix;

		display: block;

		padding: 0 20px;
	}
	@include media('<sm') {
		padding: 0 0 0 20px;
	}
	@include media('<xs') {
		padding: 10px 20px 0;
	}

	display: flex;

	padding: 0 40px;

	align-items: center;
	flex-wrap: wrap;
	justify-content: space-between;

	&__logo {
		@include media('>=sm','<md') {
			float: left;

			width: calc(100% - 220px);
			padding: 20px 0 0;
		}
		@include media('<sm') {
			font-size: 32px;

			width: calc(100% - 250px);
			padding: 0 20px;
		}
		@include media('<xs') {
			font-size: 24px;

			width: calc(100% - 30px);
			padding: 0 30px 0 0;

			text-align: center;
		}

		font-family: $pfdintextcondpro;
		font-size: 40px;
		font-weight: 500;
		line-height: 1;
	}

	&__logo-link {
		text-decoration: none;

		span:nth-child(1) {
			color: $dodgerblue;
		}

		span:nth-child(2) {
			color: $chileanfire;
		}

		span:nth-child(3) {
			color: $shark;
		}
	}

	&__contacts {
		@include media('>=sm','<md') {
			float: right;
		}
		@include media('<xs') {
			display: flex;

			width: calc(100% + 40px);
			margin: 10px -20px 0;
		}

		width: 220px;

		order: 1;
	}

	&__tel {
		@include media('<sm') {
			padding: 10px 20px;
		}
		@include media('<xs') {
			width: 50%;
			padding: 10px;
		}

		font-family: $opensans;
		font-size: 16px;
		font-weight: 700;
		line-height: 1.25;

		display: block;

		padding: 20px;

		text-align: center;
		text-decoration: none;

		color: $shark;
		background: $linkwater;

		body:not(.mobile) &:hover {
			color: $shark;
		}
	}

	&__callorder {
		@include media('<sm') {
			padding: 10px 20px;
		}
		@include media('<xs') {
			width: 50%;
			padding: 10px;
		}

		font-family: $opensans;
		font-size: 13px;
		font-weight: 700;
		line-height: 1.5385;
		line-height: 1.5385;

		display: block;

		width: 100%;
		padding: 20px;

		cursor: pointer;
		transition: background $t;
		text-align: center;
		text-decoration: none;

		color: white;
		border: 0;
		outline: none;
		background: $chileanfire;

		body:not(.mobile) &:hover {
			color: white;
			background: lighten($chileanfire, 5%);
		}
	}
}
