.contacts {
	@include media('<md') {
		display: block;
	}

	display: flex;

	&__info {
		@include media('<md') {
			width: auto;
			padding: 40px 20px;
		}

		width: 512px;
		padding: 80px 40px;
	}

	&__title {
		@include media('<md') {
			margin-bottom: 20px;
		}

		margin-bottom: 40px;
	}

	&__group {
		@include margin-bottom(15px);
	}

	&__label {
		@include media('<sm') {
			font-size: 16px;
		}

		font-family: $oswald;
		font-size: 20px;
		font-weight: 700;
		line-height: 1.5;
	}

	&__value {
		font-family: $opensans;
	}

	&__map {
		@include media('<md') {
			width: auto;
			height: 280px;
		}

		width: calc(100% - 512px);
	}
}
