.text {
	p {
		@include margin-bottom(1.25em);

		margin: 0;
	}

	ul {
		@include reset-list;
		@include margin-bottom(1.25em);

		& > li {
			padding-left: 32px;

			&::before {
				display: block;
				float: left;

				width: 4px;
				height: 4px;
				margin: 9px 0 0 -12px;

				content: '';

				border-radius: 50%;
				background: $chileanfire;
			}
		}
	}
}
