.services-nav {
	@include reset-list;

	&__item {
		@include margin-top(20px);
	}

	&__link {
		@include media('<sm') {
			font-size: 16px;
			line-height: 1.25;

			min-height: 0;
		}

		font-family: $oswald;
		font-size: 20px;
		line-height: 1;

		position: relative;

		display: flex;

		min-height: 60px;
		padding: 10px 20px;

		transition: color $t, background $t;
		text-decoration: none;

		color: white;
		background: $shark;

		align-items: center;

		body:not(.mobile) &:hover,
		&._active {
			color: $shark;
			background: $dodgerblue;
		}

		&._active::before {
			@include triangle(10px 10px, $dodgerblue, up-left);

			position: absolute;
			top: 100%;
			left: 10px;

			content: '';
		}
	}

	&__link-text {
		display: block;
	}

	&__submenu {
		@include reset-list;

		font-size: 14px;
		line-height: 1.2858;

		padding: 17px 0 0;
	}

	&__submenu-item {
		padding: 3px 0 3px 22px;

		&::before {
			display: block;
			float: left;

			width: 4px;
			height: 4px;
			margin: 8px 0 0 -12px;

			content: '';

			border-radius: 50%;
			background: $shark;
		}

		&._active {
			background: $dodgerblue;
		}
	}

	&__submenu-link {
		.services-nav__submenu-item._active & {
			text-decoration: none;

			color: white;
		}
	}
}
