.specification {
	&__group {
		@include media('<md') {
			justify-content: space-between;
		}

		display: flex;

		&:nth-child(2n+1) {
			background: $linkwater;
		}

		&:nth-child(2n):last-child {
			border-bottom: 1px solid $linkwater;
		}
	}

	&__label {
		@include media('<md') {
			width: auto;
			padding: 10px;

			text-align: left;
		}

		width: 60%;
		padding: 10px 20px;

		text-align: right;
	}

	&__value {
		@include media('<md') {
			width: auto;
			padding: 10px;

			white-space: nowrap;
		}

		width: 40%;
		padding: 10px 20px;
	}
}
