*,
:before,
:after {
	box-sizing: border-box;
}

:focus {
	outline: none;
}

::-moz-focus-inner {
	border: 0;
}

html {
	font-family: $ptsans;
	font-size: 16px;
	line-height: 1.25;

	display: flex;

	min-height: 100%;

	color: $shark;
	background: $mercury;

	&.fixed {
		overflow-y: hidden;

		height: 100%;
	}

	&.has-scrollbar {
		overflow-y: scroll;
	}
}

body {
	position: relative;

	display: flex;
	flex-direction: column;

	width: 100%;
	min-width: 320px;

	align-items: center;

	html.fixed & {
		overflow: hidden;
	}
}

a {
	transition: color $t;

	color: $azureradiance;

	body:not(.mobile) &:hover {
		text-decoration: none;

		color: $chileanfire;
	}
}

p {
	@include margin-bottom(1.25em);

	margin: 0;
}

h1 {
	@include media('<md') {
		font-size: 32px;
		line-height: 1.25;
	}
	@include media('<sm') {
		font-size: 24px;
	}

	font-family: $oswald;
	font-size: 34px;
	font-weight: 400;
	line-height: 1;

	margin: 0 0 30px;
}

h2 {
	@include margin-top(40px);
	@include margin-bottom(20px);
	@include media('<md') {
		font-size: 24px;
	}
	@include media('<sm') {
		font-size: 20px;
	}

	font-family: $oswald;
	font-size: 28px;
	font-weight: 400;
	line-height: 1.25;

	margin: 0;
}

.text-center {
	text-align: center;
}

.text-left {
	text-align: left;
}

.text-right {
	text-align: right;
}


