.bottom-nav {
	@include media('<md') {
		padding: 20px 13px;
	}
	@include media('<xs') {
		padding: 20px;
	}

	padding: 40px 33px;

	background: $yellowsea;

	&__list {
		@include reset-list;
		@include media('<xs') {
			display: block;
		}

		display: flex;

		flex-wrap: wrap;
	}

	&__item {
		@include media('<xs') {
			margin: 0;
		}

		margin: 0 7px;

		&._caption {
			font-weight: 700;
		}

		&._separator {
			@include media('<md') {
				width: calc(100% - 14px);
				height: 10px;
			}

			&::before {
				@include media('>=md') {
					content: '\007C';
				}
			}
		}
	}

	&__link {
		color: inherit;

		body:not(.mobile) &:hover {
			text-decoration: none;

			color: inherit;
		}
	}
}
