.benefits {
	@include media('<md') {
		padding: 40px 20px 0;
	}

	padding: 80px 40px 0;

	background: $mercury;

	&__list {
		@include media('<md') {
			margin: 20px -20px 0;
		}

		display: flex;

		margin: 40px -40px 0;

		flex-wrap: wrap;
	}

	&__item {
		@include media('<md') {
			padding: 30px 20px;
		}
		@include media('<sm') {
			width: 50%;
		}

		display: block;

		width: 25%;
		padding: 40px 20px;

		text-align: center;
		text-decoration: none;

		&:nth-child(2n+1) {
			@include media('>=sm') {
				color: $shark;
				background: $yellowsea;
			}

			body:not(.mobile) &:hover {
				@include media('>=sm') {
					color: $shark;
				}
			}
		}

		&:nth-child(2n) {
			@include media('>=sm') {
				color: $yellowsea;
				background: $shark;
			}

			body:not(.mobile) &:hover {
				@include media('>=sm') {
					color: $yellowsea;
				}
			}
		}

		&:nth-child(4n+1),
		&:nth-child(4n+4) {
			@include media('<sm') {
				color: $shark;
				background: $yellowsea;
			}

			body:not(.mobile) &:hover {
				@include media('<sm') {
					color: $shark;
				}
			}
		}

		&:nth-child(4n+2),
		&:nth-child(4n+3) {
			@include media('<sm') {
				color: $yellowsea;
				background: $shark;
			}

			body:not(.mobile) &:hover {
				@include media('<sm') {
					color: $yellowsea;
				}
			}
		}
	}

	&__icon {
		@include media('<md') {
			width: 60px;
		}

		display: block;

		max-width: 100%;
		height: auto;
		margin: 0 auto;

		fill: currentColor;
	}

	&__caption {
		@include media('<xs') {
			font-size: 13px;
		}

		font-size: 16px;
		line-height: 1.375;

		max-width: 186px;
		margin: 15px auto 0;

		transition: transform $t;

		body:not(.mobile) .benefits__item:hover & {
			transform: translate3d(0,10px,0);
		}
	}
}
