.entry {
	@include media('<sm') {
		font-size: 16px;

		padding: 12px 10px;
	}

	font-size: 20px;
	line-height: 1.5;

	display: block;

	width: 100%;
	padding: 14px 20px;

	transition: border-color $t;

	border: 1px solid $silver;
	outline: none;

	&::placeholder {
		opacity: .6;
	}

	&:focus {
		border-color: $dovegray;
	}
}
