.articles {
	&__item {
		@include margin-bottom(20px);
		@include media('<xs') {
			display: block;
		}

		display: flex;
	}

	&__date {
		@include media('<xs') {
			width: auto;
		}

		font-size: 14px;
		line-height: 1.4286;

		width: 100px;

		color: $dustygray;
	}

	&__caption {
		@include media('<xs') {
			width: auto;
		}

		width: calc(100% - 100px);
	}
}
