.offer {
	@include media('<md') {
		margin: 0 0 20px 20px;
	}
	@include media('<xs') {
		display: flex;
		float: none;

		width: 100%;
		margin: 0 0 20px;
	}

	float: right;

	width: 200px;
	margin: 0 0 40px 40px;

	&__hero {
		@include media('<xs') {
			display: none;
		}

		background: $linkwater;
	}

	&__image {
		display: block;

		max-width: 100%;
		height: auto;
		margin: 0 auto;
	}

	&__order {
		@include media('<xs') {
			width: 50%;
		}
	}

	&__prices {
		@include media('<xs') {
			width: 50%;
			margin-top: 0;
		}

		margin-top: 20px;
	}
}
