.form {
	&__title {
		@include media('<md') {
			font-size: 32px;
			line-height: 1.25;

			margin-bottom: 20px;
		}
		@include media('<sm') {
			font-size: 24px;
		}

		font-family: $oswald;
		font-size: 44px;
		line-height: 1;

		margin-bottom: 40px;

		text-align: center;
	}

	&__group {
		@include margin-bottom(40px);
		@include media('<md') {
			@include margin-bottom(20px);
		}
	}
}
