.content {
	@include media('<md') {
		padding: 40px 20px;
	}

	display: flex;

	padding: 80px 40px;

	flex-wrap: wrap;

	.crumbs + & {
		@include media('>=md') {
			padding-top: 60px;
		}
	}

	&__main {
		width: 100%;

		.content__aside + & {
			@include media('<md') {
				padding: 0 0 0 30px;
			}
			@include media('<sm') {
				width: 100%;
				padding: 0;
			}

			width: calc(100% - 200px);
			padding: 0 0 0 60px;
		}
	}

	&__aside {
		@include media('<sm') {
			width: 100%;
			margin-top: 40px;

			order: 1;
		}

		width: 200px;
	}
}
